export const getSubmissionsValidationsInfo = async (
  campaign: string
): Promise<{
  submissions: ApiSubmissionInfo[]
  validations: ApiValidationsInfo[]
}> => {
  if (!apiHost) return { submissions: [], validations: [] }

  return axios
    .post(
      `${apiHost}/submissions-validations-info`,
      {
        wallet: campaign
      },
      {
        headers: { Authorization: authKey }
      }
    )
    .then((response: ApiResponse) => {
      if (!!response.data && Array.isArray(response.data)) {
        return {
          submissions: response.data[0].map((submission: any) => {
            return {
              timestamp: Math.round(Date.parse(String(submission.timestamp).concat('.000Z')) / 1000),
              kind: (Object.values(PHRASE_TYPE).find((row) => row.val === Number(submission.kind)) ?? PHRASE_TYPE.specific)['label'],
              data: submission.data,
              canonical: submission.reference_id,
              builder: submission.builder,
              submitted: submission.utterance ? true : false,
              pubkey: submission.utterance ?? undefined,
              finish: submission.finish,
              correct: submission.correct,
              incorrect: submission.incorrect
            }
          }),
          validations: response.data[1].map((validation: any) => {
            return {
              timestamp: Math.round(Date.parse(String(validation.timestamp).concat('.000Z')) / 1000),
              utterance: validation.utterance,
              validator: validation.validator,
              vote: validation.vote,
              confidence: validation.vote_confidence,
              pubkey: validation.validation
            }
          })
        }
      } else {
        return { submissions: [], validations: [] }
      }
    })
    .catch((error: any) => {
      console.log(error)
      return { submissions: [], validations: [] }
    })
}

